.chat-box {
  background: rgba(255, 255, 255, 0.2) !important; /* 배경 투명도 */
  overflow-y: scroll;
  padding: 2rem 1rem;
  border-radius: 20px;
}

.chat-box-scroll {
  overflow-y: auto;
  /* Chrome, Edge, Safari 등 웹킷 기반 브라우저용 스크롤바 숨김 */
  &::-webkit-scrollbar {
    display: none;
  }
  
  /* Firefox용 스크롤바 숨김 */
  scrollbar-width: none;
  
  /* IE, Edge(구버전)용 스크롤바 숨김 */
  -ms-overflow-style: none;
}